yout.base.less can be correctly correctly converted to scss with less2sass (ie keep it basic)\n\n// Width variables (appears count calculates by raw css)\n@width0: 100%; // Appears 3 times\n@width1: 20px; // Appears 2 times\n@width2: 100px; // Appears 1 time\n@width3: 14px; // Appears 1 time\n@width4: 18px; // Appears 1 time\n@width5: 15px; // Appears 1 time\n@width6: 2px; // Appears 1 time\n\n// Height variables (appears count calculates by raw css)\n@height0: 100%; // Appears 4 times\n@height1: 20px; // Appears 2 times\n@height2: 14px; // Appears 2 times\n@height3: 10px; // Appears 1 time\n@height4: 19px; // Appears 1 time\n@height5: 18px; // Appears 1 time\n@height6: 15px; // Appears 1 time\n\n.lm_root {\n  position: relative;\n}\n\n.lm_row > .lm_item {\n  float: left;\n}\n\n// Single Pane content (area in which final dragged content is contained)\n.lm_content {\n  overflow: hidden;\n  position: relative;\n}\n\n// \".lm_dragging\" is applied to BODY tag during Drag and is also directly applied to the root of the object being dragged\n.lm_dragging,\n.lm_dragging * {\n  cursor: move !important;\n  user-select: none;\n}\n\n// If a specific Pane is maximized\n.lm_maximised {\n  position: absolute;\n  top: 0;\n  left: 0;\n  z-index: 40;\n}\n\n.lm_maximise_placeholder {\n  display: none;\n}\n\n// Separator line (handle to change pane size)\n.lm_splitter {\n  position: relative;\n  z-index: 2;\n  touch-action: none;\n\n  &.lm_vertical {\n    .lm_drag_handle {\n      width: @width0;\n      position: absolute;\n      cursor: ns-resize;\n      touch-action: none;\n      user-select: none;\n    }\n  }\n\n  &.lm_horizontal {\n    float: left;\n    height: @height0;\n\n    .lm_drag_handle {\n      height: @height0;\n      position: absolute;\n      cursor: ew-resize;\n      touch-action: none;\n      user-select: none;\n    }\n  }\n}\n\n// Pane Header (container of Tabs for each pane)\n.lm_header {\n  overflow: visible;\n  position: relative;\n  z-index: 1;\n  user-select: none;\n\n  [class^=lm_] {\n    box-sizing: content-box !important;\n  }\n\n  // Pane controls (popout, maximize, minimize, close)\n  .lm_controls {\n    position: absolute;\n    right: 3px;\n    display: flex;\n\n    > * {\n      cursor: pointer;\n      float: left;\n      width: @width4;\n      height: @height5;\n      text-align: center;\n    }\n  }\n\n  .lm_tabs {\n    position: absolute;\n    display: flex;\n  }\n\n  // Single Tab container. A single Tab is set for each pane, a group of Tabs are contained in \".lm_header\"\n  .lm_tab {\n    cursor: pointer;\n    float: left;\n    height: @height2;\n    margin-top: 1px;\n    padding: 0px 10px 5px;\n    padding-right: 25px;\n    position: relative;\n    touch-action: none;\n\n    i {\n      width: @width6;\n      height: @height4;\n      position: absolute;\n\n      &.lm_left {\n        top: 0;\n        left: -2px;\n      }\n\n      &.lm_right {\n        top: 0;\n        right: -2px;\n      }\n    }\n\n    .lm_title {\n      display: inline-block;\n      overflow: hidden;\n      text-overflow: ellipsis;\n    }\n\n    // Close Tab Icon\n    .lm_close_tab {\n      width: @width3;\n      height: @height2;\n      position: absolute;\n      top: 0;\n      right: 0;\n      text-align: center;\n    }\n  }\n}\n\n// Change stack style to absolute positioning for docking transition ability\n.lm_stack {\n  position: relative;\n\n  > .lm_items {\n      overflow: hidden;\n  }\n\n  &.lm_left {\n    > .lm_items {\n      position: absolute;\n      left: 20px;\n      top: 0;\n    }\n  }\n  &.lm_right {\n    > .lm_items {\n      position: absolute;\n      right: 20px;\n      top: 0;\n    }\n\n    > .lm_header {\n      position: absolute;\n      right: 0;\n      top: 0;\n    }\n  }\n  &.lm_bottom {\n    > .lm_items {\n      position: absolute;\n      bottom: 20px;\n    }\n\n    > .lm_header {\n      position: absolute;\n      bottom: 0;\n    }\n  }\n}\n\n// Headers positions\n.lm_left,\n.lm_right {\n  &.lm_stack {\n    .lm_header {\n      height: 100%;\n    }\n  }\n\n  &.lm_dragProxy {\n    .lm_header,\n    .lm_items {\n      float: left;\n    }\n  }\n\n  &.lm_dragProxy,\n  &.lm_stack {\n    .lm_header {\n      width: 20px;\n      vertical-align: top;\n\n      .lm_tabs {\n        transform-origin: left top;\n        top: 0;\n        width: 1000px; /*hack*/\n      }\n\n      .lm_controls {\n        bottom: 0;\n        flex-flow: column;\n      }\n    }\n  }\n}\n\n.lm_dragProxy.lm_left,\n.lm_stack.lm_left {\n  .lm_header {\n    .lm_tabs {\n      transform: rotate(-90deg) scaleX(-1);\n      left: 0;\n      .lm_tab {\n        transform: scaleX(-1);\n        margin-top: 1px;\n      }\n    }\n\n    .lm_tabdropdown_list {\n      top:initial;\n      right:initial;\n      left:20px;\n    }\n  }\n}\n\n.lm_dragProxy.lm_right .lm_content {\n  float: left;\n}\n\n.lm_dragProxy.lm_right,\n.lm_stack.lm_right {\n  .lm_header {\n    .lm_tabs {\n      transform: rotate(90deg) scaleX(1);\n      left: 100%;\n      margin-left: 0;\n    }\n\n    .lm_controls {\n      left: 3px;\n    }\n\n    .lm_tabdropdown_list {\n      top:initial;\n      right:20px;\n    }\n  }\n}\n\n.lm_dragProxy.lm_bottom,\n.lm_stack.lm_bottom {\n  .lm_header {\n    width: 100%;\n    .lm_tab {\n      margin-top:0;\n      border-top: none;\n    }\n\n    .lm_controls {\n      top: 3px;\n    }\n\n    .lm_tabdropdown_list {\n      top:initial;\n      bottom:20px;\n    }\n  }\n}\n\n.lm_drop_tab_placeholder {\n  float: left;\n  width: @width2;\n  visibility: hidden;\n}\n\n// Dropdown arrow for additional tabs when too many to be displayed\n.lm_header {\n  .lm_controls .lm_tabdropdown:before {\n    content: '';\n    width: 0;\n    height: 0;\n    vertical-align: middle;\n    display: inline-block;\n    border-top: 5px dashed;\n    border-right: 5px solid transparent;\n    border-left: 5px solid transparent;\n    color: white; // Overriden in specific Themes\n  }\n\n  .lm_tabdropdown_list {\n    position: absolute;\n    top: 20px;\n    right: 0;\n    z-i